import { connect } from 'react-redux';
import { normalize } from 'normalizr';
import moment from 'moment';

import Search from '../../components/panels/Search';
import { search as searchSchema } from '../../schema';
import { getDoctor, getSearch, getSearchParams } from '../../selectors';
import actions from '../../actions';

const makeDesciption = ({ date, city, station }) => {
  const dateDescription = moment(date)
    .locale('ru')
    .calendar(null, {
      sameDay: '[cегодня]',
      nextDay: '[завтра]',
      nextWeek: 'D MMMM',
      sameElse: 'D MMMM'
    });
  const cityDescription = `в городе ${city.Name}`;
  const description = [dateDescription, cityDescription];
  if (station) {
    const stationDescription = `на станции ${station.Name}`;
    description.push(stationDescription);
  }
  return description.join(', ');
};

const mapStateToProps = (state, ownProps) => {
  const search = getSearch(state);
  const { entities, result } = normalize(search.results, searchSchema);
  const query = getSearchParams(state);
  return {
    ...ownProps,
    search: {
      title: query.speciality.NamePlural,
      isLoading: search.isLoading,
      results: result.map(id => getDoctor(entities, id)),
      description: makeDesciption(query),
      hasMore: search.results.length < search.total,
      page: search.page
    },
    query
  };
};

const mapDispatchToProps = dispatch => ({ dispatch });

const mergeProps = (stateProps, dispatchProps, ownProps) => {
  const { dispatch } = dispatchProps;
  const {
    query,
    search: { page }
  } = stateProps;
  return {
    ...stateProps,
    ...ownProps,
    loadMore: () => {
      dispatch({
        type: actions.SEARCH,
        payload: {
          city: query.city.Id,
          speciality: query.speciality.Id,
          when: query.date,
          subwayStation: query.station && query.station.Id,
          page: page + 1
        }
      });
    }
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps,
  mergeProps
)(Search);
